html, body {
  font-family: 'Montserrat', sans-serif; }

.header {
  position: fixed;
  top: 0;
  z-index: 1000;
  padding: 1.5em 0 1em 0;
  width: 100vw; }

.header-links {
  text-decoration: none;
  color: #3e76d5;
  font-size: 1.7em;
  padding-left: 2em;
  padding-right: 2em;
  transition: color .5s;
  border-bottom: none;
  font-weight: 500; }
  .header-links:hover {
    color: black; }

.sticky {
  position: fixed;
  animation: transitionBar;
  animation-duration: 1s;
  animation-delay: 0.1s;
  animation-fill-mode: forwards;
  width: 100%;
  z-index: 1000; }

@keyframes transitionBar {
  0% {
    background-color: transparent; }
  100% {
    background-color: white; } }

.footer {
  display: flex;
  flex-direction: column;
  margin-bottom: 3em; }

.footer-links {
  padding: .25em 4em;
  text-decoration: none;
  margin-bottom: 3em;
  color: black; }

.footer-links:hover {
  color: #5d8950; }

.footer-one,
.footer-two {
  border-right: 1px solid black; }

.footer-first {
  margin: 3em 0  .2em 0;
  display: flex;
  justify-content: center; }

.footer-second,
.footer-third {
  display: flex;
  justify-content: center; }

.footer-second p {
  border-top: 1px solid black;
  padding: 2.5em 1.8em 1em 1.8em; }

.hero-one {
  width: 100vw; }

.hero-one-name {
  position: absolute;
  top: 30vh;
  font-size: 4rem;
  color: white;
  left: calc(50vh - 85px);
  animation: transitionName;
  animation-duration: 1.5s;
  animation-delay: 0.3s;
  animation-fill-mode: forwards;
  opacity: 0;
  text-transform: uppercase; }

@keyframes transitionName {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.SectionOne {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid #A9A9A9;
  padding-bottom: 2em;
  margin-left: 2em;
  margin-right: 2em; }

.SectionOne-top {
  display: flex;
  flex-direction: column;
  text-align: center; }
  .SectionOne-top h2 {
    font-size: 2.5em; }
  .SectionOne-top p {
    width: 50%;
    margin: 0 auto 2em auto;
    font-size: 1.1em; }

.SectionOne-bottom {
  display: flex;
  justify-content: space-evenly;
  margin-left: 2em;
  margin-right: 2em; }

.HomePageCard {
  display: flex;
  width: 20vw; }
  .HomePageCard img {
    width: 60px;
    height: 40px;
    align-self: flex-start;
    margin-top: 1em;
    margin-right: 1em; }

.SectionTwo {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid 	#A9A9A9;
  margin-left: 2em;
  margin-right: 2em;
  padding-bottom: 5em;
  padding-top: 2em; }

.SectionTwo-top {
  display: flex;
  flex-direction: column;
  text-align: center; }
  .SectionTwo-top h2 {
    font-size: 2.5em; }
  .SectionTwo-top p {
    width: 50%;
    margin: 0 auto 2em auto;
    font-size: 1.1em; }

.SectionTwo-bottom {
  display: flex;
  justify-content: space-evenly; }

.SectionTwo-bottom-image-one {
  height: 40vh;
  margin-left: 1em; }

.SectionTwo-bottom-image-two {
  height: 40vh;
  margin-left: 1em; }

.SectionTwo-bottom-image-three {
  height: 40vh; }

.SectionThree {
  padding-bottom: 4em;
  margin-left: 2em;
  margin-right: 2em; }
  .SectionThree h2 {
    text-align: center;
    font-size: 2.5em; }

.SectionThree-bottom {
  display: flex;
  justify-content: space-evenly; }

.SectionThreeCard {
  border: .5px solid black;
  width: 26vw;
  padding: 1em; }
  .SectionThreeCard p,
  .SectionThreeCard h3 {
    text-align: center; }
  .SectionThreeCard hr {
    width: 50%; }

hr {
  width: 95%; }

.Port-SectionOne {
  margin-top: 3em; }

.ThumbNail {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-bottom: 2em;
  margin-top: 2em; }

.ThumbNail-left {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .ThumbNail-left h2 {
    font-size: 2.5em; }
  .ThumbNail-left p {
    width: 35vw;
    text-align: center;
    margin: 0 auto 1.7em auto; }
  .ThumbNail-left a {
    background-color: #3e76d5;
    text-decoration: none;
    color: white;
    text-align: center;
    padding: 1em;
