    margin: 0 auto; }

.ThumbNail-right {
  display: flex;
  flex-direction: column; }
  .ThumbNail-right img {
    width: 25vw;
    margin-bottom: 1em; }
  .ThumbNail-right a {
    margin: 0 auto;
    text-decoration: none;
    color: black; }

.TechCard {
  width: 80vw;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 0 auto; }

.TechCard-ind {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .TechCard-ind h3 {
    text-align: center; }
  .TechCard-ind img {
    margin-bottom: 1em;
    margin-top: 1em;
    width: 8vw; }

.About-SectionOne {
  margin-top: 5em;
  display: flex;
  justify-content: space-evenly;
